'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Badge } from '@/components/ui/badge'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import {
  Sheet,
  SheetContent,
  SheetDescription,
  SheetHeader,
  SheetTitle
} from '@/components/ui/sheet'
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow
} from '@/components/ui/table'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import {
  mockProducts,
  mockProductOptions,
  mockPolicies,
  type Product,
  type ProductOption,
  type Policy
} from '@/lib/mock/settings'
import { 
  Search, 
  Plus, 
  Edit, 
  FileText, 
  CheckCircle, 
  XCircle,
  Package,
  Settings,
  Shield,
  TrendingUp,
  DollarSign,
  Layers
} from 'lucide-react'
import { toast } from 'sonner'
import { formatCurrency, cn } from '@/lib/utils'
import { format } from 'date-fns'
import { ko } from 'date-fns/locale'

export default function ProductsSettingsPage() {
  const [products, setProducts] = useState<Product[]>(mockProducts)
  const [options, setOptions] = useState<ProductOption[]>(mockProductOptions)
  const [policies, setPolicies] = useState<Policy[]>(mockPolicies)

  const [searchTerm, setSearchTerm] = useState('')
  const [drawerOpen, setDrawerOpen] = useState(false)
  const [drawerMode, setDrawerMode] = useState<'create' | 'edit'>('create')
  const [selectedItem, setSelectedItem] = useState<Product | ProductOption | Policy | null>(null)
  const [activeTab, setActiveTab] = useState<'products' | 'options' | 'policies'>('products')

  // Mock search (filters by name)
  const filteredProducts = products.filter(p => 
    p.name.toLowerCase().includes(searchTerm.toLowerCase())
  )
  const filteredOptions = options.filter(o =>
    o.name.toLowerCase().includes(searchTerm.toLowerCase())
  )
  const filteredPolicies = policies.filter(p =>
    p.name.toLowerCase().includes(searchTerm.toLowerCase())
  )

  const handleCreate = () => {
    setDrawerMode('create')
    setSelectedItem(null)
    setDrawerOpen(true)
  }

  const handleEdit = (item: Product | ProductOption | Policy) => {
    setDrawerMode('edit')
    setSelectedItem(item)
    setDrawerOpen(true)
  }

  const handleSave = () => {
    toast.success(
      drawerMode === 'create' ? '새 항목이 생성되었습니다' : '변경사항이 저장되었습니다'
    )
    setDrawerOpen(false)
  }

  const handleDelete = (id: string) => {
    if (activeTab === 'products') {
      setProducts(prev => prev.filter(p => p.id !== id))
    } else if (activeTab === 'options') {
      setOptions(prev => prev.filter(o => o.id !== id))
    } else {
      setPolicies(prev => prev.filter(p => p.id !== id))
    }
    toast.success('삭제되었습니다')
  }

  const getCategoryLabel = (category: Product['category']) => {
    const labels = {
      wedding: '웨딩',
      studio: '스튜디오',
      event: '이벤트',
      commercial: '상업'
    }
    return labels[category]
  }

  const getTypeLabel = (type: ProductOption['type']) => {
    return type === 'addon' ? '추가옵션' : '업그레이드'
  }

  const getPolicyTypeLabel = (type: Policy['type']) => {
    const labels = {
      cancellation: '취소/환불',
      refund: '환불',
      usage: '이용약관',
      privacy: '개인정보'
    }
    return labels[type]
  }

  // Calculate statistics
  const activeProducts = products.filter(p => p.isActive).length
  const activeOptions = options.filter(o => o.isActive).length
  const activePolicies = policies.filter(p => p.isActive).length
  
  const totalRevenue = products
    .filter(p => p.isActive)
    .reduce((sum, p) => sum + p.basePrice, 0)

  return (
    <div className="space-y-4 md:space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4">
        <div>
          <h1 className="text-2xl md:text-3xl font-bold tracking-tight">상품 관리</h1>
          <p className="text-sm md:text-base text-muted-foreground">
            상품, 옵션 및 정책을 관리하고 설정하세요
          </p>
        </div>
        <Button onClick={handleCreate} className="w-full sm:w-auto">
          <Plus className="mr-2 h-4 w-4" />
          새로 만들기
        </Button>
      </div>

      {/* KPI Cards */}
      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
        <Card className="hover:shadow-md transition-shadow">
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">활성 상품</CardTitle>
            <Package className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{activeProducts}</div>
            <p className="text-xs text-muted-foreground">
              전체 {products.length}개 중
            </p>
          </CardContent>
        </Card>

        <Card className="hover:shadow-md transition-shadow">
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">활성 옵션</CardTitle>
            <Layers className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{activeOptions}</div>
            <p className="text-xs text-muted-foreground">
              전체 {options.length}개 중
            </p>
          </CardContent>
        </Card>

        <Card className="hover:shadow-md transition-shadow">
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">활성 정책</CardTitle>
            <Shield className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{activePolicies}</div>
            <p className="text-xs text-muted-foreground">
              전체 {policies.length}개 중
            </p>
          </CardContent>
        </Card>

        <Card className="hover:shadow-md transition-shadow">
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">상품 총액</CardTitle>
            <DollarSign className="h-4 w-4 text-muted-foreground" />
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{formatCurrency(totalRevenue)}</div>
            <p className="text-xs text-muted-foreground">
              활성 상품 기준
            </p>
          </CardContent>
        </Card>
      </div>

      {/* Search & Actions */}
      <div className="flex items-center justify-between gap-4">
        <div className="relative flex-1">
          <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
          <Input
            placeholder="검색..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="pl-9"
          />
        </div>
      </div>

      {/* Tabs - Mobile Optimized */}
      <Tabs value={activeTab} onValueChange={(v) => setActiveTab(v as 'products' | 'options' | 'policies')}>
        <TabsList className="grid w-full grid-cols-3">
          <TabsTrigger value="products" className="flex items-center gap-1 md:gap-2 text-xs md:text-sm">
            <Package className="h-3 w-3 md:h-4 md:w-4" />
            <span className="hidden sm:inline">상품</span>
            <span className="sm:hidden">상품</span>
            <span className="hidden md:inline">({products.length})</span>
          </TabsTrigger>
          <TabsTrigger value="options" className="flex items-center gap-1 md:gap-2 text-xs md:text-sm">
            <Settings className="h-3 w-3 md:h-4 md:w-4" />
            <span className="hidden sm:inline">옵션</span>
            <span className="sm:hidden">옵션</span>
            <span className="hidden md:inline">({options.length})</span>
          </TabsTrigger>
          <TabsTrigger value="policies" className="flex items-center gap-1 md:gap-2 text-xs md:text-sm">
            <Shield className="h-3 w-3 md:h-4 md:w-4" />
            <span className="hidden sm:inline">정책</span>
            <span className="sm:hidden">정책</span>
            <span className="hidden md:inline">({policies.length})</span>
          </TabsTrigger>
        </TabsList>

        {/* Products Tab */}
        <TabsContent value="products" className="space-y-4">
          <Card>
            <CardContent className="p-0">
              <div className="overflow-x-auto">
                <Table>
                  <TableHeader>
                    <TableRow className="hover:bg-transparent">
                      <TableHead className="min-w-[150px]">상품명</TableHead>
                      <TableHead className="min-w-[100px]">카테고리</TableHead>
                      <TableHead className="min-w-[120px]">기본가격</TableHead>
                      <TableHead className="min-w-[100px]">최대 선택</TableHead>
                      <TableHead className="min-w-[100px]">납품기간</TableHead>
                      <TableHead className="min-w-[100px]">상태</TableHead>
                      <TableHead className="text-right min-w-[80px]">작업</TableHead>
                    </TableRow>
                  </TableHeader>
                <TableBody>
                  {filteredProducts.length === 0 ? (
                    <TableRow className="hover:bg-transparent">
                      <TableCell colSpan={7}>
                        <div className="flex flex-col items-center justify-center py-12 text-center">
                          <Package className="h-12 w-12 text-muted-foreground mb-4 opacity-50" />
                          <h3 className="text-base md:text-lg font-semibold mb-1">상품이 없습니다</h3>
                          <p className="text-sm text-muted-foreground mb-4">
                            {searchTerm ? '검색 결과가 없습니다' : '새 상품을 추가하여 시작하세요'}
                          </p>
                          {!searchTerm && (
                            <Button onClick={handleCreate} variant="outline" size="sm">
                              <Plus className="mr-2 h-4 w-4" />
                              첫 상품 만들기
                            </Button>
                          )}
                        </div>
                      </TableCell>
                    </TableRow>
                  ) : (
                    filteredProducts.map((product) => (
                      <TableRow key={product.id} className="hover:bg-muted/50 transition-colors">
                        <TableCell className="font-medium">{product.name}</TableCell>
                        <TableCell>
                          <Badge variant="secondary">{getCategoryLabel(product.category)}</Badge>
                        </TableCell>
                        <TableCell className="font-medium">{formatCurrency(product.basePrice)}</TableCell>
                        <TableCell>
                          <span className="text-muted-foreground">{product.maxProofSelections}장</span>
                        </TableCell>
                        <TableCell>
                          <span className="text-muted-foreground">{product.turnAroundDays}일</span>
                        </TableCell>
                        <TableCell>
                          {product.isActive ? (
                            <Badge variant="default" className="bg-green-600 hover:bg-green-700">
                              <CheckCircle className="mr-1 h-3 w-3" />
                              활성
                            </Badge>
                          ) : (
                            <Badge variant="secondary">
                              <XCircle className="mr-1 h-3 w-3" />
                              비활성
                            </Badge>
                          )}
                        </TableCell>
                        <TableCell className="text-right">
                          <Button
                            variant="ghost"
                            size="sm"
                            onClick={() => handleEdit(product)}
                            className="hover:bg-muted"
                          >
                            <Edit className="h-4 w-4" />
                          </Button>
                        </TableCell>
                      </TableRow>
                    ))
                  )}
                </TableBody>
              </Table>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        {/* Options Tab */}
        <TabsContent value="options" className="space-y-4">
          <Card>
            <CardContent className="p-0">
              <div className="overflow-x-auto">
                <Table>
                  <TableHeader>
                    <TableRow className="hover:bg-transparent">
                      <TableHead className="min-w-[150px]">옵션명</TableHead>
                      <TableHead className="min-w-[100px]">타입</TableHead>
                      <TableHead className="min-w-[120px]">가격</TableHead>
                      <TableHead className="min-w-[100px]">적용 상품</TableHead>
                      <TableHead className="min-w-[100px]">상태</TableHead>
                      <TableHead className="text-right min-w-[80px]">작업</TableHead>
                    </TableRow>
                  </TableHeader>
                <TableBody>
                  {filteredOptions.length === 0 ? (
                    <TableRow className="hover:bg-transparent">
                      <TableCell colSpan={6}>
                        <div className="flex flex-col items-center justify-center py-12 text-center">
                          <Settings className="h-12 w-12 text-muted-foreground mb-4 opacity-50" />
                          <h3 className="text-base md:text-lg font-semibold mb-1">옵션이 없습니다</h3>
                          <p className="text-sm text-muted-foreground mb-4">
                            {searchTerm ? '검색 결과가 없습니다' : '새 옵션을 추가하여 시작하세요'}
                          </p>
                          {!searchTerm && (
                            <Button onClick={handleCreate} variant="outline" size="sm">
                              <Plus className="mr-2 h-4 w-4" />
                              첫 옵션 만들기
                            </Button>
                          )}
                        </div>
                      </TableCell>
                    </TableRow>
                  ) : (
                    filteredOptions.map((option) => (
                      <TableRow key={option.id} className="hover:bg-muted/50 transition-colors">
                        <TableCell className="font-medium">{option.name}</TableCell>
                        <TableCell>
                          <Badge variant="outline" className={cn(
                            option.type === 'addon' 
                              ? 'border-blue-200 bg-blue-50 text-blue-700' 
                              : 'border-purple-200 bg-purple-50 text-purple-700'
                          )}>
                            {getTypeLabel(option.type)}
                          </Badge>
                        </TableCell>
                        <TableCell className="font-medium">{formatCurrency(option.price)}</TableCell>
                        <TableCell>
                          <span className="text-sm text-muted-foreground">
                            {option.applicableProducts.length}개 상품
                          </span>
                        </TableCell>
                        <TableCell>
                          {option.isActive ? (
                            <Badge variant="default" className="bg-green-600 hover:bg-green-700">
                              <CheckCircle className="mr-1 h-3 w-3" />
                              활성
                            </Badge>
                          ) : (
                            <Badge variant="secondary">
                              <XCircle className="mr-1 h-3 w-3" />
                              비활성
                            </Badge>
                          )}
                        </TableCell>
                        <TableCell className="text-right">
                          <Button
                            variant="ghost"
                            size="sm"
                            onClick={() => handleEdit(option)}
                            className="hover:bg-muted"
                          >
                            <Edit className="h-4 w-4" />
                          </Button>
                        </TableCell>
                      </TableRow>
                    ))
                  )}
                </TableBody>
              </Table>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        {/* Policies Tab */}
        <TabsContent value="policies" className="space-y-4">
          <Card>
            <CardContent className="p-0">
              <div className="overflow-x-auto">
                <Table>
                  <TableHeader>
                    <TableRow className="hover:bg-transparent">
                      <TableHead className="min-w-[150px]">정책명</TableHead>
                      <TableHead className="min-w-[100px]">타입</TableHead>
                      <TableHead className="min-w-[80px]">버전</TableHead>
                      <TableHead className="min-w-[100px]">시행일</TableHead>
                      <TableHead className="min-w-[100px]">상태</TableHead>
                      <TableHead className="text-right min-w-[80px]">작업</TableHead>
                    </TableRow>
                  </TableHeader>
                <TableBody>
                  {filteredPolicies.length === 0 ? (
                    <TableRow className="hover:bg-transparent">
                      <TableCell colSpan={6}>
                        <div className="flex flex-col items-center justify-center py-12 text-center">
                          <Shield className="h-12 w-12 text-muted-foreground mb-4 opacity-50" />
                          <h3 className="text-base md:text-lg font-semibold mb-1">정책이 없습니다</h3>
                          <p className="text-sm text-muted-foreground mb-4">
                            {searchTerm ? '검색 결과가 없습니다' : '새 정책을 추가하여 시작하세요'}
                          </p>
                          {!searchTerm && (
                            <Button onClick={handleCreate} variant="outline" size="sm">
                              <Plus className="mr-2 h-4 w-4" />
                              첫 정책 만들기
                            </Button>
                          )}
                        </div>
                      </TableCell>
                    </TableRow>
                  ) : (
                    filteredPolicies.map((policy) => (
                      <TableRow key={policy.id} className="hover:bg-muted/50 transition-colors">
                        <TableCell className="font-medium">{policy.name}</TableCell>
                        <TableCell>
                          <Badge 
                            variant="secondary" 
                            className={cn(
                              policy.type === 'cancellation' && 'border-orange-200 bg-orange-50 text-orange-700',
                              policy.type === 'privacy' && 'border-indigo-200 bg-indigo-50 text-indigo-700',
                              policy.type === 'usage' && 'border-teal-200 bg-teal-50 text-teal-700',
                              policy.type === 'refund' && 'border-red-200 bg-red-50 text-red-700'
                            )}
                          >
                            {getPolicyTypeLabel(policy.type)}
                          </Badge>
                        </TableCell>
                        <TableCell>
                          <Badge variant="outline" className="font-mono text-xs">
                            {policy.version}
                          </Badge>
                        </TableCell>
                        <TableCell className="text-muted-foreground">
                          {format(new Date(policy.effectiveDate), 'yyyy.MM.dd', { locale: ko })}
                        </TableCell>
                        <TableCell>
                          {policy.isActive ? (
                            <Badge variant="default" className="bg-green-600 hover:bg-green-700">
                              <CheckCircle className="mr-1 h-3 w-3" />
                              활성
                            </Badge>
                          ) : (
                            <Badge variant="secondary">
                              <XCircle className="mr-1 h-3 w-3" />
                              비활성
                            </Badge>
                          )}
                        </TableCell>
                        <TableCell className="text-right">
                          <Button
                            variant="ghost"
                            size="sm"
                            onClick={() => handleEdit(policy)}
                            className="hover:bg-muted"
                          >
                            <FileText className="h-4 w-4" />
                          </Button>
                        </TableCell>
                      </TableRow>
                    ))
                  )}
                </TableBody>
              </Table>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>

      {/* Drawer for Create/Edit (Mock) */}
      <Sheet open={drawerOpen} onOpenChange={setDrawerOpen}>
        <SheetContent className="w-full sm:max-w-2xl overflow-y-auto">
          <SheetHeader>
            <div className="flex items-center gap-2">
              {activeTab === 'products' && <Package className="h-5 w-5 text-primary" />}
              {activeTab === 'options' && <Settings className="h-5 w-5 text-primary" />}
              {activeTab === 'policies' && <Shield className="h-5 w-5 text-primary" />}
              <SheetTitle className="text-xl">
                {drawerMode === 'create' ? '새로 만들기' : '수정하기'}
              </SheetTitle>
            </div>
            <SheetDescription>
              {activeTab === 'products' && '상품 정보를 입력하고 관리하세요'}
              {activeTab === 'options' && '옵션 정보를 입력하고 관리하세요'}
              {activeTab === 'policies' && '정책 정보를 입력하고 관리하세요'}
            </SheetDescription>
          </SheetHeader>

          <div className="mt-8 space-y-6">
            <Card className="border-dashed">
              <CardContent className="pt-6">
                <div className="flex items-start gap-3 text-sm">
                  <div className="rounded-full bg-blue-100 p-2">
                    <FileText className="h-4 w-4 text-blue-600" />
                  </div>
                  <div>
                    <p className="font-medium text-blue-900 mb-1">Mock Editor</p>
                    <p className="text-muted-foreground">
                      실제 폼이 여기에 구현됩니다. 각 탭에 맞는 입력 필드들이 표시됩니다.
                    </p>
                  </div>
                </div>
              </CardContent>
            </Card>

            {selectedItem && (
              <Card>
                <CardHeader>
                  <CardTitle className="text-sm font-medium">선택된 항목 정보</CardTitle>
                </CardHeader>
                <CardContent>
                  <pre className="rounded-lg bg-muted p-4 text-xs overflow-auto max-h-96">
                    {JSON.stringify(selectedItem, null, 2)}
                  </pre>
                </CardContent>
              </Card>
            )}

            {!selectedItem && drawerMode === 'create' && (
              <div className="space-y-4">
                <div className="rounded-lg border-2 border-dashed border-muted-foreground/25 p-8 text-center">
                  <div className="mx-auto w-12 h-12 rounded-full bg-muted flex items-center justify-center mb-3">
                    <Plus className="h-6 w-6 text-muted-foreground" />
                  </div>
                  <h4 className="font-medium mb-1">새 항목 생성</h4>
                  <p className="text-sm text-muted-foreground">
                    {activeTab === 'products' && '새로운 상품을 추가하세요'}
                    {activeTab === 'options' && '새로운 옵션을 추가하세요'}
                    {activeTab === 'policies' && '새로운 정책을 추가하세요'}
                  </p>
                </div>
              </div>
            )}
          </div>

          <div className="mt-8 flex flex-col-reverse sm:flex-row justify-end gap-2 sm:gap-3 pt-6 border-t">
            <Button variant="outline" onClick={() => setDrawerOpen(false)} className="w-full sm:w-auto">
              취소
            </Button>
            <Button onClick={handleSave} className="w-full sm:w-auto">
              {drawerMode === 'create' ? (
                <>
                  <Plus className="mr-2 h-4 w-4" />
                  생성
                </>
              ) : (
                <>
                  <CheckCircle className="mr-2 h-4 w-4" />
                  저장
                </>
              )}
            </Button>
          </div>
        </SheetContent>
      </Sheet>
    </div>
  )
}

